 html,
 body {
   margin: 0;
   padding: 0;
   display: flex;
   flex-flow: column wrap;
   align-items: center;
   justify-content: center;
   overflow: hidden;
   /* 禁用滚动条 */

   div {
     color: green;
     align-items: center;
   }

   background-size: cover;
   background-repeat: no-repeat;
   background-position: center;
   width: 100vw;
   /* 修改单位为vw */
   height: 100vh;
   /* 修改单位为vh */
 }

 // 開始頁面
 .foundButton {
   padding: 2vw 4vw;
   /* 上下左右各 2vw 的内边距 */
   border-radius: 9px;
   background: #d5f365;
   border: none;
   font-family: inherit;
   text-align: center;
   color: #2e3730;
   font-weight: bold;
   font-size: 2vw;
   /* 修改单位为vw */
   display: flex;
   /* 使用 flex 布局 */
   justify-content: center;
   /* 水平居中 */
   align-items: center;
   /* 垂直居中 */
   position: relative;
   left: 50%;
   /* 左边距离为页面宽度的一半 */
   transform: translate(-50%, -50%);
   /* 平移以实现居中 */
 }

 .centered-image {
   display: flex;
   /* 确保图像以块级元素显示 */
   margin: 0 auto;
   /* 设置左右边距为 auto，实现水平居中 */
   width: 50dvw;
   /* 修改单位为vw */
   max-width: 100dvw;
   /* 修改单位为vw */
 }


 // 對話框樣式
 .custom-modal {
   width: 80vw;
   /* 调整为适当的宽度 */
   height: 0;
   /* 调整对话框的高度 */
   border-radius: 10px;
   background-color: #f0f2f5;
   position: fixed;
   /* 将对话框固定在视口中 */
   top: 10vw;
   left: 50%;
   /* 将对话框水平居中 */
   transform: translateX(-50%);
   /* 通过平移来实现水平居中 */
   display: flex;
   flex-direction: column;
   /* 将对话框内部内容以垂直方向排列 */
 }

 .custom-modal-image {
   position: absolute;
   bottom: 0;
   /* 将图像定位在标题容器底部 */
   left: 3vw;
   /* 修改为相对于视口宽度的百分比 */
   width: 20vw;
   /* 修改为相对于视口宽度的百分比 */
   transform: translateX(-50%);
   /* 通过平移来实现水平居中 */
   height: auto;
   max-width: none;
   /* 允许图片超出容器 */
 }

 .custom-modal-title {
   font-size: 2.7vw;
   /* 修改为相对于视口宽度的百分比 */
   padding: 0.5vw;
   /* 修改为相对于视口宽度的百分比 */
   border-radius: 2vw;
   /* 修改为相对于视口宽度的百分比 */
   font-weight: bold;
   margin-bottom: 0.5dvw;
   /* 修改单位为vw */
   color: white;
   background-color: #88c070;
   position: absolute;
   z-index: 0;
   left: 0.5vw;
   bottom: 0;
 }

 .custom-modal-button {
   border: 0.1dvw solid #2e3730;
   /* 修改单位为vw */
   color: #2e3730;
 }

 .custom-modal-content {
   text-align: center;
   font-size: 2.5vw;
   /* 修改为相对于视口宽度的百分比 */
   padding-left: 10vw;
   width: 65vw;
   bottom: 0;

 }

 .custom-modal-countdown {
   text-align: center;
   font-size: 2vw;
   /* 修改单位为vw */
   margin-top: 2vw;
   /* 修改单位为vw */
 }